                            time: '08/2015 - Present',
                            company: 'Ho chi minh City university of technology',
                            description: 'The HCM city University of Technology at 268 Ly Thuong Kiet Street District 10 Ho Chi Minh City, Viet Nam. My major is Computer Science. GPA 7.5+'
