                            time: '08/2013 - 08/2015',
                            company: 'Trng THPT Chu Vn An',
                            description: 'Trng THPT Chu Vn An ti ng 100 Hng Vng, th x Gia Ngha, tnh k Nng.'
                        }, {
                            position: 'i hc',
                            time: '08/2015 - Present',
                            company: 'Trng i hc Bch Khoa TP.HCM',
                            description: 'Trng i hc Bch Khoa TP.HCM ti 268 L Thng Kit, Qun 10, Thnh ph HCM, Vit Nam. Chuyn ngnh Khoa Hc My Tnh. GPA 7.5+'
                        }
                    ]
                }
            }
        }
    }
}
</script>

