<template>
    <div class="widgets experience">
        <div class="title-widgets">
            <h2>{{data[lang].title}}</h2>
        </div>
        <ul class="content" :data="JSON.stringify(skills)">
            <li v-for="(item, index) in skills" :key="index">
                <span>{{item.text}}</span>
                <span
                class="percent"
                :style="`
                    --percent: ${item.percent + '%'};
                `"
                ></span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'skill',
    props: {
        lang: {
            type: String,
            default: 'en'
        }
    },
    data() {
        return {
            data: {
                en: {
                    title: 'Experiences & Skills',
                },
                vi: {
                    title: 'Kinh nghim & k nng',
                }
            },
            skills: [
                { text: 'HTML/HTML5', percent: 95},
                { text: 'CSS/CSS3', percent: 95},
                { text: 'Jquery/JS', percent: 75},
                { text: 'Bootstrap', percent: 90},
                { text: 'ReactJS', percent: 60},
                { text: 'VueJS', percent: 70},
                { text: 'PSD/UI/UX', percent: 50},
                { text: 'Responsive', percent: 90},
                { text: 'Less/Sass', percent: 80},
                { text: 'Golang', percent: 60},
                { text: 'PHP', percent: 50},
                { text: 'NodeJs', percent: 70},
            ]
        }
    }
}
</script>

<style scoped>
    .experience .content {
        list-style: none;
    }
    .experience .content li {
        font-size: 0;
    }
    .experience .content li + li {
        margin-top: 5px;
    }
    .experience .content li span {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        position: relative;
    }
    .experience .content li span:first-child {
        width: 40%;
    }

    .experience .content li .percent {
        width: 60%;
    }
    .experience .content li .percent:before {
        content: '';
        width: 100%;
        height: 7px;
        display: block;
        background-color: #fff;
    }
    .experience .content li .percent:after {
        content: '';
        width: var(--percent);
        display: block;
        height: 7px;
        position: absolute;
        background-color: #29abe2;
        top: 0;
        left: 0;
    }
</style>



