<template>
    <div class="widgets-content hobbies">
        <div class="main-title">
            <h2>{{data[lang].title}}</h2>
        </div>
        <ul class="content">
            <li v-for="item in data[lang].hobbies" :key="item.text">
                <img :src="item.icon" :alt="item.text">
                <span>{{item.text}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
import movie from '../assets/movie.png';
import music from '../assets/music.png';
import sing from '../assets/sing.png';
import travel from '../assets/travel.png';
export default {
    name: 'hobbies',
    props: {
        lang: {
            type: String,
            default: 'en'
        }
    },
    data() {
        return {
            data: {
                en: {
                    title: 'Hobbies',
                    hobbies: [
                        { icon: music, text: 'Music'},
                        { icon: sing, text: 'Sing'},
                        { icon: movie, text: 'Movie'},
                        { icon: travel, text: 'Travel'},
                    ]
                },
                vi: {
                    title: 'S thch',
                    hobbies: [
                        { icon: music, text: 'Nghe nhc'},
                        { icon: sing, text: 'Ht h'},
                        { icon: movie, text: 'Xem phim'},
                        { icon: travel, text: 'Du lch'},
                    ]
                }
            }
        }
    }
}
</script>

<style scoped>
    .hobbies .content {
        padding: 0 40px;
        padding-top: 20px;
    }
    .hobbies ul li {
        margin-right: 80px;
        display: inline-block;
    }
    .hobbies ul li:last-child {
        margin-left: 0;
    }
    .hobbies ul li img,
    .hobbies ul li span {
        display: block;
        margin: 0 auto;
    }
    .hobbies ul li img {
        height: 35px;
    }
    .hobbies ul li span {
        text-align: center;
        font-weight: 600;
        margin-top: 8px;
    }
</style>



