<template>
    <div class="main-content">
        <div class="intro-content">
            <h1 class="name">{{ data[lang].name }}</h1>
            <p class="position">Front End Developer</p>
            <p class="description">{{ data[lang].description }}</p>
        </div>
        <experience :lang="lang" />
        <education :lang="lang" />
        <hobbies :lang="lang" />
    </div>
</template>

<script>
import Experience from './Experience.vue';
import Education from './Education.vue';
import Hobbies from './Hobbies.vue';
export default {
    name: 'main-content',
    components: {
        Experience,
        Education,
        Hobbies
    },
    props: {
        lang: {
            type: String,
            default: 'en'
        }
    },
    data() {
        return {
            data: {
                en: {
                    name: "Tran Cong Luc",
                    description: "Take advantage of my logical thinking, being curious and learning new things. When I was a sophomore at Ho Chi Minh City Polytechnic University, I self-studied about Front End Website developer and done Freelancer jobs related to Front End for more than a year. It is an early motivation to motivate yourself to become a Full Stack Developer in the future and bring a lot value to people. In addition, I also like teaching, based on accumulated knowledge, I were working as a Mentor about Front End Website at http://hocvoichuyengia.com"
                },
                vi: {
                    name: "Trn Cng Lc",
                    description: "Tn dng kh nng t duy logic ca bn thn, kh nng t tm hi, hc hi nhng ci mi. Khi ti cn l mt cu sinh vin nm hai ti trng i hc Bch Khoa Thnh ph H Ch Minh, ti  t hc v lp trnh vin Front End Website v lm cc cng vic Freelancer lin quan n Front End khong hn mt nm.  l ng lc sm thc y bn thn  tr thnh mt Full Stack Developer trong tng lai. Ngoi ra ti cng thch dy hc, da vo kin thc tch lu c, ti ang lm Mentor v Front End Website ti http://hocvoichuyengia.com"
                }
            }
        }
    }
}
</script>

<style>
    #main-content {
        display: table-cell;
        vertical-align: top;
    }
    .intro-content {
        margin-bottom: 27px;
        padding: 20px 0 0 30px;
    }
    .intro-content .name {
        font-size: 45px;
        color: #29bbec;
        font-weight: 700;
        letter-spacing: 4px;
        position: relative;
        line-height: 1.1;
    }
    .intro-content .name:after {
        content: '';
        height: 3px;
        width: 50px;
        display: block;
        background-color: #29bbec;
    }
    .intro-content .position {
        font-size: 19px;
        text-transform: uppercase;
        font-weight: 600;
        letter-spacing: 2px;
        margin-top: 10px;
    }
    .intro-content .description {
        font-size: 16px;
        font-weight: 600;
        margin-top: 15px;
        line-height: 1.5;
    }
    .widgets-content + .widgets-content {
        margin-top: 50px;
    }
    .widgets-content .main-title {
        position: relative;
        background-color: #19282e;
        color: #fff;
        height: 40px;
        line-height: 39px;
        margin-bottom: 25px;
    }
    .widgets-content .main-title h2 {
        font-weight: 400;
        font-size: 16px;
        text-transform: uppercase;
        padding-left: 31px;
        position: relative;
    }
    .widgets-content .main-title h2:after {
        content: '';
        width: 19px;
        height: 2px;
        display: block;
        background-color: #fff;
        position: absolute;
        bottom: 8px;
        left: 32px;
    }

    .list-info {
        padding:  0 31px;
    }
    .exp-block {
        display: flex;
    }
    .exp-block + .exp-block {
        margin-top: 25px;
    }
    .exp-block .position {
        font-weight: bold;
        font-size: 13px;
        margin-bottom: 8px;
    }
    .exp-block .time {
        font-size: 15px;
    }
    .exp-block .company {
        font-size: 13px;
        margin-bottom: 10px;
        font-weight: 600;
        text-transform: uppercase;
    }
    .exp-block .description {
        font-size: 14px;
    }
    .exp-block .left {
        flex-basis: 33.3333%;
    }
    .exp-block .right {
        flex-basis: 66.6666%;
    }
</style>


