<template>
    <div class="widgets personal">
        <div class="title-widgets">
            <h2>{{data[lang].title}}</h2>
        </div>
        <ul class="content">
            <li v-for="(item, index) in data[lang].infos" :key="index">
                <span>{{item.text}}</span>
                <span>{{item.value}}</span>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'personal',
    props: {
        lang: {
            type: String,
            default: 'en'
        }
    },
    data() {
        return {
            data: {
                en: {
                    title: 'Personal Information',
                    infos: [
                        {
                            text: 'Birthday',
                            value: '19th February 1997'
                        }, {
                            text: 'Relationship',
                            value: 'Single'
                        }, {
                            text: 'Residence',
                            value: 'Ho Chi Minh City',
                        }, {
                            text: 'Languages',
                            value: 'VietNamese, English'
                        }
                    ]
                },
                vi: {
                    title: 'Thng tin c nhn',
                    infos: [
                        {
                            text: 'Ngy sinh',
                            value: '19/02/1997'
                        }, {
                            text: 'Hn nhn',
                            value: 'c thn'
                        }, {
                            text: 'Ni ',
                            value: 'TP. H Ch Minh',
                        }, {
                            text: 'Ngn ng',
                            value: 'Ting vit, Ting anh'
                        }
                    ]
                }
            }
        }
    }
}
</script>

<style scoped>
    .personal .content {
        list-style: none;
    }
    .personal .content li {
        font-size: 0;
    }
    .personal .content li + li {
        margin-top: 5px;
    }
    .personal .content li span {
        display: inline-block;
        vertical-align: middle;
        font-size: 14px;
        position: relative;
    }
    .personal .content li span:first-child {
        width: 40%;
    }
    .personal .content li span:first-child:after {
        content: ':';
        display: inline-block;
        position: absolute;
        right: 12px;
    }
    .personal .content li span:last-child {
        width: 60%;
    }
</style>



