<template>
    <div id="sidebar">
        <profile :lang="lang"/>
        <div class="wrapper-widgets">
            <personal-vision :lang="lang"/>
            <personal :lang="lang" />
            <skill :lang="lang" />
        </div>
    </div>
</template>

<script>
import Profile from './Profile.vue';
import PersonalVision from './PersonalVision.vue';
import Personal from './Personal.vue';
import Skill from './Skill.vue';
export default {
    name: 'side-bar',
    components: {
        Skill,
        Profile,
        Personal,
        PersonalVision
    },
    props: {
        lang: {
            type: String,
            default: 'en'
        }
    },
    data() {
        return {

        }
    }
}
</script>

<style>
    #sidebar {
        display: table-cell;
        vertical-align: top;
        width: 33.3333%;
        background-color: #1c222f;
        min-height: 900px;
        color: #fff;
    }
    .wrapper-widgets {
        padding: 60px 25px;
    }
    .title-widgets {
        position: relative;
        margin-bottom: 13px;
    }
    .title-widgets:after {
        content: '';
        display: block;
        width: 20px;
        height: 2px;
        background-color: #fff;
    }
    .title-widgets h2 {
        font-size: 17px;
        font-weight: 600;
        text-transform: uppercase;
    }

    .widgets .content {
        font-size: 14px;
        font-weight: 300;
    }
    .widgets + .widgets {
        margin-top: 65px;
    }
</style>


