<template>
  <div id="app">
    <div class="container">
      <sidebar :lang="lang"/>
      <main-content :lang="lang"/>
    </div>
    <ul class="lang">
      <li v-on:click="lang = 'vi'"><img :src="VI" alt="Vit Nam"><span>Vit Nam</span></li>
      <li v-on:click="lang = 'en'"><img :src="EN" alt="English"><span>English</span></li>
    </ul>
  </div>
</template>

<script>
import VI from './assets/Vietnam.png';
import EN from './assets/UK.png';

import Sidebar from './components/Sidebar.vue';
import MainContent from './components/MainContent.vue';
export default {
  name: 'app',
  components: {
    Sidebar,
    MainContent
  },
  data () {
    return {
      lang: 'en',
      VI,
      EN
    }
  },
  created() {
    //console.log("test");
  }
}
</script>

<style>
  #app {
    padding: 20px 0;
    position: relative;
  }
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  body {
    font-size: 14px;
    color: #19282e;
    font-weight: 400;
    line-height: 1.425;
    font-family: 'Open Sans', sans-serif;
  }
  .container {
    width: 1000px;
    margin: 0 auto;
    padding-top: 20px;
    padding-bottom: 20px;
    padding-left: 20px;
    display: table;
    box-shadow: 1px 1px 6px 0 rgba(28, 34, 47, 0.5);
  }
  img {
    max-width: 100%;
  }
  .lang {
    position: fixed;
    right: 0;
    top: 50%;
    list-style: none;;
    -webkit-transform: translate(85px,-50%);
    -ms-transform: translate(85px,-50%);
    -o-transform: translate(85px,-50%);
    transform: translate(85px,-50%);
  }
  .lang li {
    background-color: #1c222f;
    padding: 10px;
    position: relative;
    width: 140px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    -webkit-transform: translateX(0);
    -ms-transform: translateX(0);
    -o-transform: translateX(0);
    transform: translateX(0);
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease;
    cursor: pointer;
  }
  .lang li:hover {
    -webkit-transform: translateX(-85px);
    -ms-transform: translateX(-85px);
    -o-transform: translateX(-85px);
    transform: translateX(-85px);
  }
  .lang li + li {
    margin-top: 2px;
  }
  .lang li img {
    width: 35px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
  }
  .lang li span {
    color: #fff;
    display: inline-block;
    vertical-align: middle;
    padding-left: 10px;
  }
  .lang li span:before {
    content: '';
    left: 55px;
    top: 0;
    height: 100%;
    width: 1px;
    background-color: #fff;
    display: block;
    position: absolute;
  }
</style>



