<template>
    <div class="profile">
        <div class="avatar">
            <a target="_blank" href="https://www.facebook.com/congluc1902">
                <img :src="avatar" alt="Tran Cong Luc">
            </a>
        </div>
        <div class="block-content">
            <div class="item phone">
                <i class="icons ion-ios-telephone"></i>
                <span>{{phone}}</span>
            </div>
            <div class="item email">
                <i class="icons ion-ios-email"></i>
                <span>{{email}}</span>
            </div>
        </div>
    </div>
</template>

<script>
import avatar from '../assets/avatar.png';

export default {
    name: 'profile',
    data() {
        return {
            phone: '+84 343 261 825',
            email: 'luctc_developer1902@gmail.com',
            avatar
        }
    }
}
</script>

<style scoped>
    .avatar {
        position: relative;
    }
    .avatar:before {
        content: '';
        display: block;
        padding-top: 110%;
    }
    .avatar a {
        position: absolute;;
        display: block;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
    }
    .avatar img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .block-content .item {
        padding: 14px 20px;
        font-size: 0;
        background: rgba(114,206,237,1);
        background: -moz-linear-gradient(top, rgba(114,206,237,1) 0%, rgba(39,187,236,1) 100%);
        background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(114,206,237,1)), color-stop(100%, rgba(39,187,236,1)));
        background: -webkit-linear-gradient(top, rgba(114,206,237,1) 0%, rgba(39,187,236,1) 100%);
        background: -o-linear-gradient(top, rgba(114,206,237,1) 0%, rgba(39,187,236,1) 100%);
        background: -ms-linear-gradient(top, rgba(114,206,237,1) 0%, rgba(39,187,236,1) 100%);
        background: linear-gradient(to bottom, rgba(114,206,237,1) 0%, rgba(39,187,236,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#72ceed', endColorstr='#27bbec', GradientType=0 );
    }
    .block-content .icons {
        width: 25px;
        height: 25px;
        border: 1px solid #fff;
        border-radius: 100%;
        margin-right: 15px;
        display: inline-block;
        vertical-align: middle;
        font-size: 17px;
        line-height: 24px;
        text-align: center;
    }
    .block-content .icons + span {
        display: inline-block;
        vertical-align: middle;
        max-width: calc(100% - 40px);
        font-size: 14px;
        font-weight: 600;
    } 
</style>



